export type SignBody = {
  email: string;
  password: string;
  commandId?: string;
};

export type AuthResult = {
  token: string;
  profile: ProfileResult;
};

export type ProfileResult = {
  id?: string;
  name: string;
  email: string;
  signUpDate: Date;
  commandId?: string;
};

export type UpdateProfileBody = {
  name: string;
};

export type ServerErrors = {
  errors: {
    extensions: {
      code: ErrorCode;
    };

    name: string;
    fieldName?: string;
    stack: string;
    message: string;
  }[];
};

